
export const setWindowWidth=()=> {
    var viewportWidth = window.innerWidth;
    var windowWidth;
    if (viewportWidth <= [767]) {
        windowWidth = { width: '100%', height: "600px", backgroundColor: 'white', border: 'none', margin: 'auto', borderRadius: "0px", };
    } else {
        windowWidth = { width: '100%', height: "600px", backgroundColor: 'white', border: 'none', margin: 'auto', borderRadius: "10px", };
    }
    return windowWidth;
}


export const setInitialMapZoom=()=> {
    var viewportWidth = window.innerWidth;
    var mapZoom;
    if (viewportWidth <= [767]) {
      mapZoom = [3.75];
    } if (viewportWidth >= [768]) {
      mapZoom = [4.5];
    } if (viewportWidth >= [1600]) {
      mapZoom = [4.5];
    }
    return mapZoom;
  }

  export const setDragging=()=> {
    var viewportWidth = window.innerWidth;
    var dragging;
    if (viewportWidth <= [767]) {
      dragging = false;
    } if (viewportWidth >= [768]) {
      dragging = true;
    } 
    return dragging;
  }