import React, { useState } from 'react';
import layerIcon from "../assets/layer-control.png";

const LayerControlBtn = ({ setBaseLayers, baseLayers, layersItems, setSingleBaseLayer, singlebaseLayer, singleLayerItems }) => {

    const handleBaseLayerChange = (event) => {
        const value = event.target.value;
        if (event.target.checked) {
            setBaseLayers((prevLayers) => [...prevLayers, value]);
        } else {
            setBaseLayers((prevLayers) => prevLayers.filter((layer) => layer !== value));
        }
    };

    const handleSingleBaseLayerChange = (event) => {
        setSingleBaseLayer(event.target.value);
    };


    const [isLayerControlVisible, setLayerControlVisible] = useState(false);

    const handleLayerIconHover = () => {
        setLayerControlVisible(true);
    };

    const handleLayerControlHover = () => {
        setLayerControlVisible(true);
    };

    const handleLayerControlLeave = () => {
        setLayerControlVisible(false);
    };

    return (
        <>
            <div
                className="layer_icon"
                onMouseEnter={handleLayerIconHover}
            >
                <img src={layerIcon} alt="Layer" />
            </div>

            <div
                className={`layercontrol_container ${isLayerControlVisible ? "visible" : ""}`}
                onMouseEnter={handleLayerControlHover}
                onMouseLeave={handleLayerControlLeave}
            >

                {singleLayerItems.map((item) => (
                    <div key={item.value} className="layercontrol_item">
                        <label>
                            <input
                                type="radio"
                                value={item.value}
                                checked={singlebaseLayer === item.value}
                                onChange={handleSingleBaseLayerChange}
                            />
                            <span>{item.label}</span>
                        </label>
                    </div>
                ))}

                {layersItems.map((item) => (
                    <div key={item.value} className="layercontrol_item">
                        <label>
                            <input
                                type="checkbox"
                                value={item.value}
                                checked={baseLayers.includes(item.value)}
                                onChange={handleBaseLayerChange}
                            />
                            <span>{item.label}</span>
                        </label>
                    </div>
                ))}
            </div>
        </>
    );
};

export default LayerControlBtn;
