import React, { useRef, useState } from "react";
import { setWindowWidth } from "../utilities/Functions";
import { setInitialMapZoom } from "../utilities/Functions";
import { setDragging } from "../utilities/Functions";
import {
    MapContainer,
    useMapEvents,
    GeoJSON,
    ScaleControl, Pane
} from "react-leaflet";
import "leaflet/dist/leaflet.css";
import 'leaflet-fullscreen/dist/Leaflet.fullscreen.js';
import 'leaflet-fullscreen/dist/leaflet.fullscreen.css';
import ExportMapButton from "../components/ExportMapButton";
import LayerControlBtn from "../components/LayerControlBtn";
import Lightening_Deaths from "../data/Lightening_Deaths.json"
// import indiaBasin from '../data/indiaBasin.json';
import CloudToGroundLightningStrikes from '../data/CloudToGroundLightningStrikes.json';
import { Link } from "react-router-dom";


const Legend = ({ baseLayers, singlebaseLayer }) => {

    const Lightening_DeathsLegend = [
        { color: "#F50000", label: "> 400" },
        { color: "#F57A00", label: "300 - 400" },
        { color: "#F5F500", label: "200 - 300" },
        { color: "#F6FFDE", label: "100 - 200" },
        { color: "#82CD47", label: "50 - 100" },
        { color: "#379237", label: "< 50" },
    ];
    const CloudToGroundLightningStrikesLegend = [
        { color: "#F50000", label: "> 200000" },
        { color: "#F57A00", label: "100000 - 200000" },
        { color: "#F5F500", label: "50000 - 100000" },
        { color: "#F6FFDE", label: "10000 - 50000" },
        { color: "#82CD47", label: "1000 - 10000" },
        { color: "#379237", label: "< 1000" },
    ];


    // const indiaBasinLegend = [
    //     { color: "darkblue", label: "India Basin" },
    // ];






    return (
        <>
            {baseLayers.length > 0 && (
                <div className="legend_container">
                    <h5>Legends</h5>
                    {singlebaseLayer === "Lightening_Deaths" && (
                        <div>
                            <p>Deaths due to Lightening</p>
                            <p>(in year 2020)</p>
                            {Lightening_DeathsLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}
                    {singlebaseLayer === "CloudToGroundLightningStrikes" && (
                        <div>
                            <p>Cloud to Ground Lightning Strikes</p>
                            <p>(in year 2021)</p>
                            {CloudToGroundLightningStrikesLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )}
                    {/* {baseLayers.includes("indiaBasin") && (
                        <div>
                            {indiaBasinLegend.map((item) => (
                                <div key={item.color} className="legend_item">
                                    <span
                                        className="legend_item_square"
                                        style={{ backgroundColor: item.color }}
                                    />
                                    <span className="legend-label">{item.label}</span>
                                </div>
                            ))}
                        </div>
                    )} */}
                </div>
            )}
        </>
    );
};


const LightingMap = () => {
    const [mousePosition, setMousePosition] = useState({ lat: 0, lng: 0 });
    const mapContainerRef = useRef(null);

    const HandleMouseHover = () => {
        useMapEvents({
            mousemove: (e) => {
                setMousePosition(e.latlng);
            },
        });

        return null;
    };

    const [baseLayers, setBaseLayers] = useState(["",]);

    const layersItems = [
        // { value: "indiaBasin", label: "River Basin Boundary" },
    ];


    const [singlebaseLayer, setSingleBaseLayer] = useState("Lightening_Deaths");
    const singleLayerItems = [
        { value: "Lightening_Deaths", label: "Lightening Deaths" },
        { value: "CloudToGroundLightningStrikes", label: "Cloud to Ground Lightning Strikes" },
    ];


    const Lightening_DeathsDensity = (density => {
        return density > 400
            ? '#F50000'
            : density > 300
                ? '#F57A00'
                : density > 200
                    ? '#F5F500'
                    : density > 100
                        ? '#F6FFDE'
                        : density > 50
                            ? '#82CD47'
                            : '#379237';
    })
    const Lightening_DeathsStyle = (feature => {
        return ({
            fillColor: Lightening_DeathsDensity(feature.properties.Year_2020),
            weight: 1,
            opacity: 1,
            color: 'black',
            dashArray: '2',
            fillOpacity: 1
        });
    });

    function onEachFeatureLightening_Deaths(feature, layer) {
        layer.on("mouseover", function (e) {
            if (feature.properties && feature.properties.Name) {
                const popupContent = `
          <div>
            State: ${feature.properties.Name}<br/>
            Deaths in 2018: ${feature.properties.Year_2018}<br/>
            Deaths in 2019: ${feature.properties.Year_2019}<br/>
            Deaths in 2020: ${feature.properties.Year_2020}<br/>
          </div>
        `;
                layer.bindTooltip(popupContent, { sticky: true });
            }
            layer.openTooltip();
        });

        layer.on("mouseout", function () {
            layer.closeTooltip();
        });
    }


    const CloudToGroundLightningStrikesDensity = (density => {
        return density > 200000
            ? '#F50000'
            : density > 100000
                ? '#F57A00'
                : density > 50000
                    ? '#F5F500'
                    : density > 10000
                        ? '#F6FFDE'
                        : density > 1000
                            ? '#82CD47'
                            : '#379237';
    })
    const CloudToGroundLightningStrikesStyle = (feature => {
        return ({
            fillColor: CloudToGroundLightningStrikesDensity(feature.properties.CG),
            weight: 1,
            opacity: 1,
            color: 'black',
            dashArray: '2',
            fillOpacity: 1
        });
    });

    function onEachFeatureCloudToGroundLightningStrikes(feature, layer) {
        layer.on("mouseover", function (e) {
            if (feature.properties && feature.properties.Name) {
                const popupContent = `
          <div>
            State: ${feature.properties.Name}<br/>
            CG: ${feature.properties.CG}<br/>
          </div>
        `;
                layer.bindTooltip(popupContent, { sticky: true });
            }
            layer.openTooltip();
        });

        layer.on("mouseout", function () {
            layer.closeTooltip();
        });
    }







    return (
        <div className="main_page_container">
            <div className="row">
                <div className="col-md-3">
                    <div className="info_container">
                        <h3>Lighting</h3>
                        <p>
                            Lightning is a natural electrical discharge that occurs during thunderstorms. It happens when there is a buildup of electrical potential difference between the ground and the atmosphere, typically within a thundercloud or between different clouds. This electrical potential difference can be caused by the separation of positive and negative charges within these clouds. <br />
                            Lightning is a dangerous and unpredictable phenomenon. It can cause fires, damage property, and injure or kill people. It is important to take precautions to avoid lightning strikes, such as staying indoors during thunderstorms and avoiding open areas.
                        </p>
                        <Link to="/lighting/lighting-details"> <button className="general_button">Read More</button></Link>
                        <Link to="/quiz/lighting"> <button className="general_button">Quiz</button></Link>

                    </div>

                </div>

                <div className="col-md-9">
                    <div className="map_container" ref={mapContainerRef}>
                        <MapContainer
                            center={[22, 84]}
                            fullscreenControl={true}
                            zoom={setInitialMapZoom()}
                            style={setWindowWidth()}
                            attributionControl={false}
                            scrollWheelZoom={false}
                            zoomDelta={0.25}
                            zoomSnap={0}
                            minZoom={3}
                            keyboard={false}
                            dragging={setDragging()}
                            // touchZoom={false}
                            doubleClickZoom={false}
                            maxZoom={9}
                        >
                            <HandleMouseHover />
                            <ScaleControl />

                            <LayerControlBtn setBaseLayers={setBaseLayers} baseLayers={baseLayers} layersItems={layersItems} singlebaseLayer={singlebaseLayer} setSingleBaseLayer={setSingleBaseLayer} singleLayerItems={singleLayerItems} />


                            <Pane name="Lightening_Deaths" style={{ zIndex: -2 }}>
                                {singlebaseLayer === "Lightening_Deaths" && (
                                    <GeoJSON
                                        data={Lightening_Deaths.features}
                                        style={Lightening_DeathsStyle}
                                        onEachFeature={onEachFeatureLightening_Deaths}
                                    />
                                )}
                            </Pane>

                            <Pane name="CloudToGroundLightningStrikes" style={{ zIndex: -2 }}>
                                {singlebaseLayer === "CloudToGroundLightningStrikes" && (
                                    <GeoJSON
                                        data={CloudToGroundLightningStrikes.features}
                                        style={CloudToGroundLightningStrikesStyle}
                                        onEachFeature={onEachFeatureCloudToGroundLightningStrikes}
                                    />
                                )}
                            </Pane>

                            {/* {baseLayers.includes("indiaBasin") && (
                                <GeoJSON
                                    data={indiaBasin.features}
                                    style={{
                                        fillColor: "none",
                                        weight: 2,
                                        color: "darkblue",
                                        interactive: false,
                                    }}
                                />
                            )} */}



                            <div
                                style={{
                                    position: "absolute",
                                    bottom: 10,
                                    right: 10,
                                    backgroundColor: "white",
                                    padding: 5,
                                }}
                            >
                                Lat: {mousePosition.lat.toFixed(4)}, Long:{" "}
                                {mousePosition.lng.toFixed(4)}
                            </div>
                            <div className="desktop_legend_container">
                                {baseLayers && <Legend baseLayers={baseLayers} singlebaseLayer={singlebaseLayer} />}
                            </div>
                            <ExportMapButton mapContainerRef={mapContainerRef} mapTitle="Natural Disaster: Lighting" />
                        </MapContainer>

                        <div className="phone_legend_container">
                            {baseLayers && <Legend baseLayers={baseLayers} singlebaseLayer={singlebaseLayer} />}
                        </div>


                    </div>


                </div>
            </div>
        </div>
    )
}

export default LightingMap